@import url(https://fonts.googleapis.com/css2?family=Heebo&family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button--delete,.button--submit{text-transform:uppercase;padding:12px;display:inline-block;text-align:center;font-weight:700;transition:all 0.4s ease 0s;border:none}.button--delete:hover,.button--submit:hover{cursor:pointer;transition:all 0.4s ease 0s}.button--delete{background-color:#b33a25;color:#ffffff}.button--delete:hover{background-color:#6d0d2a}.button--submit{background-color:#257fb3;color:#ffffff}.button--submit:hover{background-color:#0d476d}.question{display:flex;flex-direction:column;align-items:center;padding:20px}.question__list{display:flex;flex-direction:column;align-items:center}.question__item{display:flex;justify-content:space-between;width:500px}.question-create{padding:20px}.question-single{padding:20px}.question__typeahead{height:100px;margin-bottom:40px}body{font-family:'Heebo', sans-serif}

